<template>
  <v-tooltip bottom>
    <template v-slot:activator="{ on }">
      <v-badge overlap left bottom color="transparent" class="move">
        <template v-slot:badge>
          <v-icon v-if="raid" color="yellow">check_circle</v-icon>
        </template>
        <button @click="toggle" v-on="on">
          <img
            v-if="value.item.img"
            :src="value.item.img"
            :class="{checked, unchecked:!checked, cramped}"
          />
          <img
            v-else
            :alt="value.item.name?value.item.name:'['+value.item.id+']'"
            class="dummy"
            :class="{checked, unchecked:!checked}"
          />
        </button>
      </v-badge>
    </template>
    <span>
      {{value.item.name}}
      <br />Required for:
      <br />
      <template v-if="raid?value.raid.quests:value.nonraid.quests">
        <span
          v-for="(q, i) in raid?value.raid.quests:value.nonraid.quests"
          :key="q.id"
        >{{q | resolveQuest | name}}{{((raid?value.raid.quests.length:value.nonraid.quests.length)>(i+1))?", ":""}}</span>
      </template>
      <br
        v-if="raid?value.raid.quests:value.nonraid.quests && raid?value.raid.quests:value.nonraid.quests"
      />
      <template v-if="raid?value.raid.quests:value.nonraid.quests">
        <span
          v-for="(q, i) in raid?value.raid.hideout:value.nonraid.hideout"
          :key="q.id"
        >{{q | resolveHideout | name}}{{((raid?value.raid.hideout.length:value.nonraid.hideout.length)>(i+1))?", ":""}}</span>
      </template>
    </span>
  </v-tooltip>
</template>
<script>
export default {
  name: "ItemBox",
  props: ["item", "value", "raid", "checked", "cramped"],
  created() {},
  data: () => ({
    on: false
  }),
  methods: {
    toggle: function() {
      var stocked = this.$store.state.stocked;
      if (!stocked[this.item]) {
        this.$set(stocked, this.item, {
          raid: 0,
          nonraid: 0
        });
      }
      if (this.raid) {
        this.$set(
          stocked[this.item],
          "raid",
          stocked[this.item].raid + (this.checked ? -1 : 1)
        );
      } else {
        this.$set(
          stocked[this.item],
          "nonraid",
          stocked[this.item].nonraid + (this.checked ? -1 : 1)
        );
      }
      this.$store.commit("setStocked", stocked);
    }
  }
};
</script>
<style>
.checked {
  filter: brightness(120%);
  outline: 1px solid #9a8866;
}

.unchecked {
  filter: sepia(100%) hue-rotate(320deg) saturate(20%) brightness(40%);
  /* outline: 1px solid black; */
}

.dummy {
  min-height: 20px;
  min-width: 20px;
}
.v-badge__badge {
  bottom: auto;
  left: auto;
  right: calc(100% - 18px) !important;
  top: calc(100% - 22px) !important;
}
button:focus {
  outline: 0;
}
button {
  padding: 1px;
}
img {
  max-width: 200px;
  max-height: 200px;
}
.cramped {
  max-height: 50px;
}
</style>